.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

.Preview {
  display: block;
  width: 90%;
  margin: 0px auto;
}

.Wrapper {
  position: relative;
  margin: 0 auto;
  width: 300px;
}

.Canvas {
  position: absolute;
  top: 0;
  left: 0;
}

.Preloader {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.Spinner {
  width: 24px;
  margin-right: 20px;
}
